@keyframes effect {
	0% {
		background: linear-gradient(
			#008000, #00FF00);
		-webkit-background-clip: text;
	}

	100% {
		background: linear-gradient(
			#3CE7D7, #000FFF);
		-webkit-background-clip: text;
	}
}
#projects div.containr div.left ul li:hover{
	animation: effect 2s  linear infinite 1s;
}
#projects div.containr div.right{
	width: 75%;
}
#projects div.containr div.right ul{
